const InitialState = ['LFIN', 'VANI', 'CINS', 'AFSL', 'HPFL', 'LGL', 'PAP', 'VLL', 'SINH', 'NHL', 'AMSL', 'CHL', 'SLND', 'TWOD', 'KDL', 'CTLD'];

export default (state = InitialState, action) => {
  switch (action.type) {
    default:
      return state;
  }
};




